export default {
	/** null */
	isNull(val) {
		return _isNull(val);
	},
	isNotNull(val) {
		return !_isNull(val);
	},
	ifNullToEmptyString(val) {
		if (_isNull(val)) {
			return '';
		}
		return val;
	},
	//  
	input: {
		checkNotEmpty(value) {
			return !_isNull(value) || ` .`;
		},
		checkNumberType(value) {
			return !isNaN(value) || ` .`;
		},
		checkMaxLength(value, restrictions) {
			const targetVal = value || '';
			const maxLen = restrictions?.maxLength || 5;
			const restrictionMsg =
				restrictions?.message || `    ${maxLen} .`;
			return targetVal.length <= maxLen || restrictionMsg;
		},
		checkEmailPattern(value, restrictions) {
			const restrictionMsg = restrictions?.message || `  .`;

			const emailPattern =
				/^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
			return emailPattern.test(value) || restrictionMsg;
		},
	},
};

// ValidationUtils    
function _isNull(val) {
	if (
		val == null ||
		val == undefined ||
		(typeof val == 'string' && val == '') ||
		(Array.isArray(val) && val.length == 0) ||
		(typeof val == 'object' && Object.keys(val).length == 0)
	) {
		return true;
	}
	return false;
}
